let idImagen;
let btnSesion = document.querySelector('#btn-sesion');
let btnRegistro = document.querySelector('#btn-registro');
let divRegistro = document.querySelector('.box-form-registro');
let divSesion = document.querySelector('.box-form-sesion');

let btnMostrarArray = document.querySelector('#mostrarArray');

document.addEventListener('DOMContentLoaded', function(){
    alternateFunctions();

});

function alternateFunctions(){
    let body = document.querySelector('body');
    // mostrarArray();

    if (body.classList.contains('cont-galeria')){
        let boton1 = document.querySelector('#back');
        let boton2 = document.querySelector('#done');
        // mostrarArray();
        getNamePlatillo();
        getURL();
        activeBtn();
        boton1.addEventListener('click', (e)=>{
            e.preventDefault();
            saveImg();
            // window.location.href ="menu-principal.php";
            window.history.back();
        });
        boton2.addEventListener('click', (e)=>{
            e.preventDefault();
            saveImg();
            window.location.href ="send-Images.php";
        });
    }

    else if(body.classList.contains('sendImages')){
        // sendImages();
        // cleanSession();
        sendImages();
        setTimeout(function(){
            sessionStorage.clear();
            // localStorage.clear();
            window.location.href ="index.php";
        }, 5000);
    }

    else if (body.classList.contains('inicio')){

        activeSubmit();
        activeSubmitSesion()
        alternarFormularios();
    }
}

function getURL(){
    const galeria = window.location.search;
    const urlParam = new URLSearchParams(galeria);
    var urlGaleria = urlParam.get('galeria');
    // var urlArchivoJSON = "./" + urlGaleria + ".json";
    var urlArchivoJSON = "json/" + urlGaleria + ".json";
    mostrarImagenes(urlArchivoJSON, urlGaleria);
}

async function mostrarImagenes(url, categoria){
    try{

        const resultado = await fetch(url);
        const db = await resultado.json();

        const {imagenes} = db;

        imagenes.forEach( imagen =>{
            const { id, nombre, ubicacion }= imagen;

            gallery(ubicacion, id, nombre, categoria);
        } )
    } catch(error){

        console.log('error al cargar imagenes');
    
    }
}

function gallery(url, id, nombreImg, categoria){
    const galeria = document.querySelector('.galeria-imagenes');

    const imagen = document.createElement('IMG');
    imagen.src = url;
    imagen.classList.add('elemento-imagen')
    imagen.dataset.imagenId = id;
    imagen.dataset.nombreImg = nombreImg;
    imagen.dataset.categoria = categoria;
    imagen.setAttribute("loading", "lazy");

     const lista = document.createElement('DIV');
     lista.classList.add('imagen');

    lista.onclick = selectedImg;
    
    lista.appendChild(imagen);
    
    galeria.appendChild(lista);
    const parrafoImagen = document.createElement('P');
    parrafoImagen.innerHTML ="Nombre del platillo"
    parrafoImagen.dataset.parrafoId = id;
    const divParrafoImagen = document.createElement('DIV');
    divParrafoImagen.classList.add('parrafoImg')
    divParrafoImagen.appendChild(parrafoImagen);
    lista.appendChild(divParrafoImagen);
}

function selectedImg(e){
    let img = e.target;
    let divImg = img.parentNode;
    
    if(divImg.classList.contains('selected')){
        divImg.classList.remove('selected');
        const id =parseInt(img.dataset.imagenId);

        removeImg(id);
        removeName(id);
    } else {
        divImg.classList.add('selected');
        divNameImg();
        const imagenObj ={
            id: parseInt(img.dataset.imagenId),
            nombreImg: img.dataset.nombreImg,
            nombreCategoria: img.dataset.categoria,
            nombrePlatillo: 'Nombre'
        }
        changeId(imagenObj.id);
        addImg(imagenObj);
    }
    activeBtn();
}
function changeId(id){
    idImagen = id;
}
function removeName(id){
    const parrafos = document.querySelectorAll('.parrafoImg p');
    parrafos.forEach(parrafo=>{
        if(parrafo.dataset.parrafoId==id){
            parrafo.innerHTML = 'Nombre del platillo';
            parrafo.parentNode.style.display = 'none';
        }
    });
}
function getNamePlatillo(){
    const inputName = document.querySelector('#nombrePlatillo');
    let btnOkName = document.querySelector('#getName');
    btnOkName.addEventListener('click', ()=>{
        const idLabel = idImagen;
        imagenes.imagen.find(imagen => imagen.id == idLabel).nombrePlatillo = inputName.value;
        changeName(idLabel, inputName.value);
    })
}
function changeName(id, newName){
    const parrafos = document.querySelectorAll('.parrafoImg p');
    parrafos.forEach(parrafo=>{
        if(parrafo.dataset.parrafoId==id){
            if(newName != ''){
                parrafo.innerHTML = newName;
                parrafo.parentNode.style.display = 'unset';
            }else{
                parrafo.innerHTML = 'Nombre del platillo';
            }
        }
    })
}

function addImg(imagenObj){
    const {imagen}=imagenes;
    imagenes.imagen =[...imagen, imagenObj];
}

function removeImg(id){
    const {imagen}=imagenes;
    imagenes.imagen=imagen.filter( imagen => imagen.id !== id );
}

function activeBtn(){
    let arrayImg = JSON.parse(sessionStorage.getItem('arrayImg'));
    let btn = document.querySelector('#done')
    const {imagen} = imagenes;

    if(imagen.length>=1){
        if(btn.classList.contains('inactive')){
            btn.classList.remove('inactive');
            btn.classList.add('active');
            btn.style.pointerEvents= 'unset';
        };
    }
    else if(arrayImg != null){
        if(arrayImg.length>=1){
            if(btn.classList.contains('inactive')){
                btn.classList.remove('inactive');
                btn.classList.add('active');
                btn.style.pointerEvents= 'unset';
            };
        }
        else{
            if(btn.classList.contains('active')){
                btn.classList.remove('active');
                btn.classList.add('inactive');
                btn.style.pointerEvents= 'none';
            };
        }
    }
    else{
        if(btn.classList.contains('active')){
            btn.classList.remove('active');
            btn.classList.add('inactive');
            btn.style.pointerEvents= 'none';
        };
    }
}

function activeSubmit(){
    let btn = document.querySelector('#getDatos');
    let nombre = document.querySelector('#nombre');
    let telefono = document.querySelector('#telefono');

    nombre.addEventListener('change', e =>{
        const inputName = e.target.value;
        telefono.addEventListener('change', e =>{
            const inputTelefono = e.target.value;
            if(inputName == 0 && inputTelefono == 0){
                if(btn.classList.contains('boton-active-block')){
                    btn.classList.remove('boton-active-block');
                    btn.classList.add('boton-inactive-block');
                    btn.style.pointerEvents= 'none';
                };
            } else{
                if(btn.classList.contains('boton-inactive-block')){
                    btn.classList.remove('boton-inactive-block');
                    btn.classList.add('boton-active-block');
                    btn.style.pointerEvents= 'unset';
                };
            }
        })
    })


}
function activeSubmitSesion(){
    let btn = document.querySelector('#sesion-getDatos');
    let nombre = document.querySelector('#sesion-nombre');
    let restaurante = document.querySelector('#sesion-restaurante');

    nombre.addEventListener('change', e =>{
        const inputName = e.target.value;
        restaurante.addEventListener('change', e =>{
            const inputRestaurante = e.target.value;
            if(inputName == 0 && inputRestaurante == 0){
                if(btn.classList.contains('boton-active-block')){
                    btn.classList.remove('boton-active-block');
                    btn.classList.add('boton-inactive-block');
                    btn.style.pointerEvents= 'none';
                };
            } else{
                if(btn.classList.contains('boton-inactive-block')){
                    btn.classList.remove('boton-inactive-block');
                    btn.classList.add('boton-active-block');
                    btn.style.pointerEvents= 'unset';
                };
            }
        })
    })


}

function alternarFormularios(){
    mostrarSesion();
    mostrarRegistro();
}

function mostrarSesion(){
    btnSesion.addEventListener('click', ()=>{
        divRegistro.style.top = '-95rem';
        divSesion.style.top = '0';
    })
}
function mostrarRegistro(){
    btnRegistro.addEventListener('click', ()=>{
        divSesion.style.top = '-95rem';
        divRegistro.style.top = '0';
    })
}

