function divNameImg() {
    let btnOkName = document.querySelector('#getName');
    let boxName = document.querySelector('.boxNameImg');
    let namePlatillo = document.querySelector('#nombrePlatillo');
    showBoxName(boxName);

    btnOkName.addEventListener('click', ()=>{
        boxName.style.top = '-95rem';
        namePlatillo.value = '';
    })
}


function showBoxName(boxName){
    boxName.style.top = '25rem'
}

function saveImg(){
    if(JSON.parse(sessionStorage.getItem('arrayImg')) === null){
        let arrayImg = imagenes.imagen
        sessionStorage.setItem('arrayImg', JSON.stringify(arrayImg));
    } else{

        let imgStorage = JSON.parse(sessionStorage.getItem('arrayImg'));
        let imgPage = imagenes.imagen;
        newArrayImg = [...imgStorage, ...imgPage];

        sessionStorage.setItem('arrayImg', JSON.stringify(newArrayImg));
    }

}
